nav {
  background-color: var(--tosca-accent);
  z-index: 10;
  height: 53px;
  position: fixed;
  top: 0;
  right: 0;
  overflow: hidden;
}

.open {
  animation: .5s linear forwards openMenu;
}

.close {
  animation: .5s linear forwards closeMenu;
}

