  background: none;
  border: none;
  align-items: center;
  margin-left: auto;
  padding: 1em;
  transition: all .3s ease-in-out;
  display: flex;
  rotate: -90deg;
}

nav button:hover {
  cursor: pointer;
}

nav ul {
  width: fit-content;
