}

hr {
  border: 1px solid var(--tosca-accent);
  margin-bottom: 1em;
}

@keyframes openMenu {
  from {
    height: 53px;
  }

  to {
    height: 55vh;
  }
}

@keyframes closeMenu {
  from {
    height: 55vh;
  }

  to {
    height: 53px;
  }
}

@media (width >= 900px) {
  nav {
    width: 100%;
